.experience-content section {
  width: 296px;
}

.experience-titles {
  text-align: center;
  font-weight: 500;
}

.experience-titles p:first-child {
  font-weight: 700;
}

.experience-content article section:first-child {
  padding-right: 20px;
  border-right: 2px solid #dcdcdc;
}

.experience-content article section:last-child {
  padding-left: 20px;
  border-left: 2px solid #dcdcdc;
}

@media only screen and (max-width: 600px) {

  .experience-content section {
    padding: 20px;
  }
  
  .experience-content article section:first-child {
    border-bottom: 2px solid #dcdcdc;
    border-right: none;
  }
  
  .experience-content article section:last-child {
    border-top: 2px solid #dcdcdc;
    border-left: none;
  }
  
  .experience-content section {
    width: 100%;
  }
  }